* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

html {
  scroll-behavior: smooth;
  font-size: 100%;
  line-height: 1.5;
}

body {
  color: #333;
  min-height: 100vh;
  background: linear-gradient(135deg, #d7eef7, #eef6f8) fixed;
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, Segoe UI, Roboto, Helvetica Neue, Arial, sans-serif;
}

img, video {
  max-width: 100%;
  height: auto;
  object-fit: cover;
  display: block;
}

ul, ol {
  list-style: none;
}

a {
  color: inherit;
  text-decoration: none;
}

button, input, textarea, select {
  font: inherit;
  background: none;
  padding: 0;
}

.container {
  width: 100%;
  max-width: 100%;
  margin: 0 auto;
  padding: 0 1rem;
}

.breed-select {
  margin: 30px auto;
  display: block;
}

.breed-image {
  border-radius: 30px;
}

.breed-name {
  margin-top: 5px;
  font-size: 36px;
}

.breed-temperament {
  margin-top: 5px;
  font-size: 24px;
  font-weight: 600;
  line-height: 1.2;
}

.breed-description {
  margin-top: 15px;
}

.loader {
  text-align: center;
  margin-top: 15px;
}

.error {
  text-align: center;
  color: red;
  margin-top: 100px;
  font-size: 24px;
  font-weight: 600;
  animation: scale 2s linear infinite, bounce .3s infinite;
}

@keyframes scale {
  0%, 100% {
    scale: 1;
  }

  50% {
    scale: 1.2;
  }
}

@keyframes bounce {
  0%, 100% {
    transform: translateY(0);
  }

  50% {
    transform: translateY(-20px);
  }
}

@media (min-width: 768px) {
  .container {
    max-width: 720px;
    width: 50%;
    justify-content: space-between;
    gap: 30px;
    display: flex;
  }

  .image-container {
    flex: 1;
    justify-content: center;
    align-items: center;
    display: flex;
  }

  .breed-container {
    flex: 1;
  }
}

@media (min-width: 1024px) {
  .container {
    max-width: 960px;
  }
}

@media (min-width: 1440px) {
  .container {
    max-width: 1200px;
  }
}

@media (min-width: 1920px) {
  .container {
    max-width: 1400px;
  }
}

